<template>
  <el-row type="flex" :gutter="25"
          v-loading="loading"
          :data="tableData"
          style="width: 100%">
    <el-col :span="4" :offset="1" :xs="0">
      <div class="grid-content ">
        <h2>民族（可识别）</h2>
        <el-divider></el-divider>
        <div class="block">
          <el-timeline>
            <el-timeline-item
                v-for="(activity, index) in data.activities"
                :key="index"
                :icon="activity.icon"
                :type="activity.type"
                :color="activity.color"
                :size="activity.size"
                :timestamp="activity.timestamp">
              {{ activity.content }}
            </el-timeline-item>
          </el-timeline>
        </div>
      </div>
    </el-col>
    <el-col :span="9" :xs="24">
      <el-empty :image-size="400"
                prop="src"
                description="暂无图片"
                :image="data.result.upload_images"></el-empty>
    </el-col>
    <el-col :span="8" :xs="24">
      <div class="grid-content bg-purple">
        <el-table
            :data="tableData"
            style="width: 100% ;margin-top:20px">
          <el-table-column
              prop="title"
              label="效果"
              width="230">
          </el-table-column>
          <el-table-column
              prop="name"
              width="230">
          </el-table-column>
        </el-table>
      </div>

      <el-col :span="4" :xs="8">
        <el-button class="button" type="text" v-if="data.result.id " @click="User_assess(data.result)">识别有误？</el-button>
      </el-col>

      <!--用于修改民族的对话框-->
      <el-dialog title="您的建议" v-model="data.dialogFormVisible">
        <el-form :model="data.form">
          <el-form-item label="我们将会认真参考您的建议：" :xs="12">
            <el-input v-model="data.form.assess" autocomplete="off" :xs="12"></el-input>
          </el-form-item>
        </el-form>
        <template #footer>
    <span class="dialog-footer">
      <el-button @click="data.dialogFormVisible=false" :xs="6">取 消</el-button>
      <el-button type="primary" @click="formFinished" :xs="6">确 定</el-button>
    </span>
        </template>
      </el-dialog>

      <el-row :gutter="150">
        <el-col :span="12">
          <el-button type="file" @click="upload" style="margin-top:100px">图片预测</el-button>
        </el-col>
        <el-col :span="12">
          <el-button @click="cropImageAndPredict" style="margin-top:100px">裁剪预测</el-button>
        </el-col>
      </el-row>
    </el-col>

  </el-row>
  <el-steps :active="4" align-center>
    <el-step title="图片上传" description="" icon="el-icon-camera"></el-step>
    <el-step title="裁剪识别" description="" icon="el-icon-edit-outline"></el-step>
    <el-step title="识别完成" description="" icon="el-icon-check"></el-step>
    <el-step title="标签修改" description="" icon="el-icon-s-order"></el-step>
  </el-steps>

  <!--裁剪图片的对话框-->
  <el-dialog
      title="裁剪图片"
      v-model="data.showCropDialog"
      width="80%">
    <vue-picture-cropper
        :boxStyle="{width: '80%', height: '80%', backgroundColor: '#f8f8f8', margin: 'auto'}"
        :img="data.cropImageUrl"
        :options="{
      viewMode: 1,
      dragMode: 'crop',
    }"/>
    <template #footer>
    <span class="dialog-footer">
      <el-button @click="data.showCropDialog = false">取 消</el-button>
      <el-button type="primary" @click="imageCropped">确 定</el-button>
    </span>
    </template>
  </el-dialog>

</template>


<script>
import constant from "@/utils/constants";
import {computed, reactive, ref} from "vue";
import Axios from "axios";
import VuePictureCropper, {cropper} from 'vue-picture-cropper'
import variables from "@/utils/variables";
import {ElLoading} from 'element-plus';


export default {
  name: 'Upload',
  components: {VuePictureCropper},
  setup() {

    // 加载图片中
/////////////////////////////////////////////////////
    let loadingCount = 0;
    let loading;
    const startLoading = () => {
      loading = ElLoading.service({
        lock: true,
        text: '识别中，请稍后，由于网络问题图片加载可能有延缓...',
        background: 'rgba(0, 0, 0, 0.7)'
      });
    };

    const endLoading = () => {
      loading.close();
    };

    const showLoading = () => {
      if (loadingCount === 0) {
        startLoading();
      }
      loadingCount += 1;
    };

    const hideLoading = () => {
      if (loadingCount <= 0) {
        return;
      }
      loadingCount -= 1;
      if (loadingCount === 0) {
        endLoading();
      }
    };
/////////////////////////////////////////////


    const data = reactive({
      activities:
          [{content: '苗族',},
            {content: '蒙古族',},
            {content: '朝鲜族',},
            {content: '亻革家族',},
            {content: '彝族',},
            {content: '藏族',},
            {content: '满族',}],

      result: {
        id: '',
        nation: '等待上传',
        time: '0s',
        accuracy: '暂无数据',
        upload_images: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg'
      },

      showCropDialog: false,
      cropImageUrl: '',

      dialogFormVisible: false,
      deleteConfirmDialogVisible: false,
      selectedImage: '',
      form: {
        assess: '',
      },

    })
    const tableData = computed(() => {
      return [
        {title: '服饰类别：', name: data.result.nation},
        {title: '耗时：', name: data.result.time},
        {title: '预测概率：', name: data.result.accuracy}]
    })


    // 开始上传，打开选择文件窗口
    async function upload() {
      const button = document.createElement('input')
      button.type = 'file'
      button.click()


      await new Promise(resolve => {
        //当用户改变input输入框内容时执行一段Javascript代码：
        button.onchange = resolve
      })
      showLoading();
      const url = constant.apiUrl + 'images/'
      const file = button.files[0]
      const form = new FormData()
      form.append('upload_images', file)
      const headers = variables.token !== '' ? {
        Authorization: 'JWT ' + variables.token,
        'Content-Type': 'multipart/form-data',
      } : {
        'Content-Type': 'multipart/form-data',
      }
      const response = await Axios.post(url, form, {
        headers
      })

      const result = response.data
      data.result.id = result.id
      data.result.accuracy = result.accuracy
      data.result.nation = result.nation+ "服饰"
      data.result.time = result.time_consuming
      data.result.upload_images = result.upload_images
      hideLoading();
    }


    async function cropImageAndPredict() {
      const button = document.createElement('input')
      button.type = 'file'
      button.click()

      await new Promise(resolve => {
        button.onchange = resolve
      })

      const file = button.files[0]
      const arrayBuffer = await file.arrayBuffer()
      const blob = new Blob([arrayBuffer])
      data.cropImageUrl = URL.createObjectURL(blob)
      data.showCropDialog = true

    }

    async function imageCropped() {
      data.showCropDialog = false
      const blob = cropper.getBlob()
      showLoading();
      const url = constant.apiUrl + 'images/'
      const form = new FormData()
      const new_name = new Date().getTime()
      form.append('upload_images', blob, new_name + 'cropped.png')
      const headers = variables.token !== '' ? {
        Authorization: 'JWT ' + variables.token,
        'Content-Type': 'multipart/form-data',
      } : {
        'Content-Type': 'multipart/form-data',
      }
      const response = await Axios.post(url, form, {
        headers,
      })
      const result = response.data
      data.result.id = result.id
      data.result.accuracy = result.accuracy
      data.result.nation = result.nation+ "服饰"
      data.result.time = result.time_consuming
      data.result.upload_images = result.upload_images
      hideLoading();
    }


    function User_assess(image) {
      data.form.assess = image.assess
      data.selectedImage = image
      data.dialogFormVisible = true
    }

    async function formFinished() {
      const assess = data.form.assess
      const url = `${constant.apiUrl}images/${data.selectedImage.id}/user-assess?assess=${assess}`
      const headers = variables.token !== '' ? {
        Authorization: 'JWT ' + variables.token,
        'Content-Type': 'multipart/form-data',
      } : {
        'Content-Type': 'multipart/form-data',
      }
      await Axios.get(url, {
        headers
      })
      data.dialogFormVisible = false
    }


    return {data, tableData, cropImageAndPredict, upload, imageCropped, User_assess, formFinished}
  },
}


</script>


<style>
.el-row {
  margin-bottom: 20px;
}

.el-row :last-child {
  margin-bottom: 0;
}

.bg-purple {
  background: #d3dce6;
}


.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

</style>
